export default {
  data() {
    return {
      // 会员赠送记录列表数据
      giveList: [],
      user_id: ""
    };
  },
  onLoad() {
    this.user_id = uni.getStorageSync("user_id");
    // 会员赠送记录接口
    this.getGive();
  },
  methods: {
    getGive() {
      this.$http
        .get("/memberPackage/getGive", { params: { user_id: this.user_id } })
        .then(res => {
          console.log(res);
          this.giveList = res.data;
          console.log(this.giveList);
        });
    }
  }
};
